@import "@/assets/variables";

.search {
  container: search / inline-size;

  @include notebook {
    container-type: normal;
  }

  &__filters,
  &__instruction {
    margin-bottom: 15px;
  }

  &__result {
    display: grid;
    grid-template-columns: 1fr;
    gap: 15px;
    max-width: 100%;

    /* Use media query instead of includes to ensure proper breakpoints */
    @media (min-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (min-width: 1024px) {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__loading-spinner {
    margin-top: 15px;
  }

  &__loading-spinner {
    opacity: 0;

    &_visible {
      opacity: 1;
    }
  }

  &__load-more {
    text-align: center;
    margin-top: 20px;

    &_invisible {
      opacity: 0;
    }
  }
}
